<template>
  <admin>
    <metatag :title="`${course.data.title} - Edit Content`"></metatag>

    <template v-slot:appbar>
      <appbar-action-buttons
        :is-form-disabled="isFormDisabled"
        :is-loading="isLoading"
        :is-not-form-prestine="isNotFormPrestine"
        :shortkey-ctrl-is-pressed="shortkeyCtrlIsPressed"
        :submit-form="submitForm"
        router-name="courses.index"
        v-model="resource"
        @item:submit="submitForm"
        >
        <template v-slot:text>
          <span v-text="trans('Update')"></span>
        </template>
      </appbar-action-buttons>
    </template>

    <page-header :back="{ to: { name: 'contents.index' }, text: trans('Back'), query: {from: $route.fullPath} }">
      <template v-slot:title>
        <span v-text="trans('Edit Content')"></span>
      </template>
    </page-header>

    <validation-observer ref="updateform" v-slot="{ handleSubmit, errors, invalid, passed }">
      <v-form
        :disabled="isLoading"
        autocomplete="false"
        enctype="multipart/form-data"
        ref="updateform-form"
        v-on:submit.prevent="handleSubmit(submit($event))"
        >
        <button ref="submit-button" type="submit" class="d-none"></button>

        <!-- Alertbox -->
        <alertbox></alertbox>
        <!-- Alertbox -->

        <v-row>
          <v-col cols="12" md="9">
            <v-card flat color="transparent">
              <v-card-text class="pa-0">
                <template v-if="isFetchingResource">
                  <skeleton-edit></skeleton-edit>
                </template>

                <v-card flat color="transparent" v-show="isFinishedFetchingResource" class="mb-3">
                  <v-row>
                    <v-col cols="12">
                      <validation-provider vid="title" :name="trans('title')" rules="required" v-slot="{ errors }">
                        <v-text-field
                          :error-messages="errors"
                          :label="trans('Title')"
                          autofocus
                          class="dt-text-field"
                          name="title"
                          outlined
                          v-model="resource.data.title"
                          >
                        </v-text-field>
                      </validation-provider>
                    </v-col>
                    <v-col cols="12">
                      <validation-provider vid="slug" rules="required" :name="trans('slug')" v-slot="{ errors }">
                        <v-text-field
                          :error-messages="errors"
                          :label="trans('Slug')"
                          hint="The slug may only contain letters, numbers, dashes, and underscores."
                          class="dt-text-field"
                          name="slug"
                          :value="slugify(resource.data.title)"
                          outlined
                          v-model="resource.data.slug"
                        ></v-text-field>
                      </validation-provider>
                    </v-col>
                    <v-col cols="12">
                      <validation-provider vid="subtitle" :name="trans('subtitle')" rules="required" v-slot="{ errors }">
                        <v-text-field
                          :error-messages="errors"
                          :label="trans('Subtitle')"
                          class="dt-text-field"
                          name="subtitle"
                          outlined
                          v-model="resource.data.subtitle"
                          >
                        </v-text-field>
                      </validation-provider>
                    </v-col>
                    <v-col cols="12">
                      <v-textarea
                        :disabled="isLoading"
                        :label="trans('Description')"
                        auto-grow
                        class="dt-text-field"
                        hide-details
                        name="description"
                        outlined
                        v-model="resource.data.description"
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-card>
              </v-card-text>

              <!-- Content Component -->
              <component
                :has-old="true"
                :is="type"
                :type="resource.data.type"
                v-model="resource.data.content"
                v-show="isFinishedFetchingResource"
              ></component>
              <!-- Content Component -->
            </v-card>

            <input type="hidden" name="user_id" :value="auth.id">
            <input type="hidden" name="course_id" :value="course.data.id">
            <input type="hidden" name="sort" :value="resource.data.sort">
            <input type="hidden" name="metadata[parent]" :value="resource.data.metadata.parent">
          </v-col>

          <v-col cols="12" md="3">
            <v-row>
              <v-col cols="12">
                <template v-if="isFetchingResource">
                  <skeleton-metainfo-card></skeleton-metainfo-card>
                </template>
                <metainfo-card v-show="isFinishedFetchingResource" :list="metaInfoCardList"></metainfo-card>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-form>
    </validation-observer>

    <v-card flat color="transparent" height="100"></v-card>
  </admin>
</template>

<script>
import $api from '@/modules/Course/routes/api'
import $auth from '@/core/Auth/auth'
import clone from 'lodash/clone'
import Content from './Models/Content'
import Course from '@/modules/Course/Models/Course'
import size from 'lodash/size'
import { mapActions, mapGetters } from 'vuex'

export default {
  beforeRouteLeave (to, from, next) {
    if (this.resource.isPrestine) {
      next()
    } else {
      this.askUserBeforeNavigatingAway(next)
    }
  },

  components: {
    SkeletonEdit: () => import('@/modules/Course/cards/SkeletonEdit'),
    SkeletonIcon: () => import('@/modules/Course/cards/SkeletonIcon'),
    TextContent: () => import('./partials/TextContent'),
    UnsupportedContent: () => import('./partials/UnsupportedContent'),
    VideoContent: () => import('./partials/VideoContent'),
    PresentationContent: () => import('./partials/PresentationContent'),
    PDFContent: () => import('./partials/PDFContent'),
    EmbedContent: () => import('./partials/EmbedContent'),
    ScormContent: () => import('./partials/ScormContent'),
    AssignmentContent: () => import('./partials/AssignmentContent'),
    ExamContent: () => import('./partials/ExamContent'),
  },

  computed: {
    type () {
      if (this.$route.query.type in this.$options.components) {
        return this.$route.query.type
      }

      return 'UnsupportedContent'
    },
    ...mapGetters({
      isDense: 'settings/fieldIsDense',
      shortkeyCtrlIsPressed: 'shortkey/ctrlIsPressed',
      progressbar: 'progressbar/progressbar',
    }),
    isInvalid () {
      return this.resource.isPrestine || this.progressbar.loading
    },
    isLoading () {
      return this.progressbar.loading
    },
    isFetchingResource () {
      return this.resource.loading
    },
    isFinishedFetchingResource () {
      return !this.resource.loading
    },
    isFormDisabled () {
      return this.isInvalid || this.resource.isPrestine
    },
    isFormPrestine () {
      return this.resource.isPrestine
    },
    isNotFormPrestine () {
      return !this.isFormPrestine
    },
    metaInfoCardList () {
      return [
        { icon: 'mdi-calendar', text: trans('Created :date', { date: this.resource.data.created }) },
        { icon: 'mdi-calendar-edit', text: trans('Modified :date', { date: this.resource.data.modified }) },
        { icon: 'mdi-account-outline', text: trans('Created by :name', { name: this.resource.data.author }) },
      ]
    },
  },

  data: () => ({
    auth: $auth.getUser(),
    course: new Course,
    loading: true,
    resource: new Content,
  }),

  methods: {
    ...mapActions({
      showAlertbox: 'alertbox/show',
      hideAlertbox: 'alertbox/hide',
      showDialog: 'dialog/show',
      hideDialog: 'dialog/hide',
      showSnackbar: 'snackbar/show',
      hideSnackbar: 'snackbar/hide',
      setProgressStatus: 'progressbar/setProgressStatus',
      showProgressbar: 'progressbar/showProgressbar',
      hideProgressbar: 'progressbar/hideProgressbar',
    }),

    askUserBeforeNavigatingAway (next) {
      this.showDialog({
        illustration: () => import('@/components/Icons/WorkingDeveloperIcon.vue'),
        title: trans('Unsaved changes will be lost'),
        text: trans('You have unsaved changes on this page. If you navigate away from this page, data will not be recovered.'),
        buttons: {
          cancel: {
            text: trans('Go Back'),
            callback: () => {
              this.hideDialog()
            },
          },
          action: {
            text: trans('Discard'),
            callback: () => {
              next()
              this.hideDialog()
            },
          },
        }
      })
    },

    parseResourceData (item) {
      let data = clone(item)

      let formData = new FormData(this.$refs['updateform-form'].$el)

      formData.append('_method', 'put')

      data = formData

      return data
    },

    parseErrors (errors) {
      this.$refs['updateform'].setErrors(errors)
      errors = Object.values(errors).flat()
      this.resource.hasErrors = errors.length
      return this.resource.errors
    },

    getParseErrors (errors) {
      errors = Object.values(errors).flat()
      this.resource.hasErrors = errors.length
      return errors
    },

    getCourseResource () {
      this.resource.loading = true
      this.showProgressbar()
      axios.get(
        $api.show(this.$route.params.id)
      ).then(response => {
        this.course.data = response.data.data
      }).finally(() => {
        this.resource.loading = false,
        this.hideProgressbar()
      })
    },

    submitForm () {
      if (!this.isFormDisabled) {
        this.$refs['submit-button'].click()
        window.scrollTo({
          top: 0,
          left: 0,
          behavior: 'smooth',
        })
      }
    },

    submit (e) {
      this.showProgressbar()
      this.hideAlertbox()
      e.preventDefault()

      axios.post(
        $api.content.update(this.resource.data.id),
        this.parseResourceData(this.resource.data), {
          onUploadProgress: (e) => {
            var progress = Math.round((e.loaded * 100) / e.total)
            this.setProgressStatus({ isUploading:true, progress: progress });
          },
        }).then(response => {
        this.showSnackbar({
          text: trans('Content updated successfully'),
        })

        this.$refs['updateform'].reset()
        this.resource.isPrestine = true

        this.$router.push({name: 'contents.index'});
      }).catch(err => {
        if (err.response.status == Response.HTTP_UNPROCESSABLE_ENTITY) {
          let errorCount = size(err.response.data.errors)

          this.$refs['updateform'].setErrors(err.response.data.errors)
          this.showErrorbox({
            text: trans(err.response.data.message),
            errors: err.response.data.errors,
          })
        }
      }).finally(() => { this.hideProgressbar() })
    },

    getResource () {
      this.resource.loading = true
      this.showProgressbar()
      axios.get(
        $api.content.show(this.$route.params.content)
      ).then(response => {
        this.resource.data = response.data.data
      }).finally(() => {
        this.resource.loading = false
        this.hideProgressbar()
        this.resource.isPrestine = true
      })
    },
  },

  mounted () {
    this.getResource()
    this.getCourseResource()
  },

  watch: {
    'resource.data': {
      handler (val) {
        this.resource.isPrestine = false
        this.resource.hasErrors = this.$refs.updateform.flags.invalid

        if (!this.resource.hasErrors) {
          this.hideAlertbox()
        }
      },
      deep: true,
    },
  },
}
</script>
